import { CLDR, CalendarDate,  RelativeTimeFieldType} from "@phensley/cldr";
import moment from 'moment';

const getCalendarToken = (myMoment: moment.Moment, now: moment.Moment) => {
  var diff = myMoment.diff(now, 'days');
  if (diff <= -365) {
    return 'sameElse';
  }
  if (diff < -6) {
    return 'lastYear';
  }
  if (diff < -1) {
    return 'lastWeek';
  }
  if (diff < 0) {
    return 'lastDay';
  }
  if (diff < 1) {
    return 'sameDay';
  }
  if (diff < 2) {
    return 'nextDay';
  }
  if (diff < 7) {
    return 'sameWeek';
  }
  if (diff < 365) {
    return 'sameYear';
  }

  return 'sameElse';
};

const DOW: RelativeTimeFieldType[] = ['sun', 'mon', 'tue', 'wed', 'thu', 'fri', 'sat'];

const getRelativeTimeField = (s: CalendarDate, e: CalendarDate, cldr: CLDR) => {
  const context = 'begin-sentence';
  const yearDiff = s.year() - e.year();
  const monthDiff = s.month() - e.month();
  const dayDiff = s.dayOfMonth() - e.dayOfMonth();
  const dow = DOW[s.dayOfWeek() - 1];

   // Decisions based on above as to what / how to format
   if (yearDiff !== 0) {
    return cldr.Calendars.formatRelativeTimeField(yearDiff, 'year', { context });
    }
    if (monthDiff !== 0) {
      return cldr.Calendars.formatRelativeTimeField(monthDiff, 'month', { context });
    }
    if (Math.abs(dayDiff) < 2) {
      // Yesterday, Today, Tomorrow
      return cldr.Calendars.formatRelativeTimeField(dayDiff, 'day', { context });
    }
    if (dayDiff > 1 && dayDiff < 7) {
      // Monday
      return cldr.Calendars.weekdays()[s.dayOfWeek()];
    }
    if (dayDiff >= 7 && dayDiff < 14) {
      // Next Monday
      return cldr.Calendars.formatRelativeTimeField(1, dow, { context });
    }
    if (dayDiff < -1 && dayDiff > -7) {
      // Last Monday
      return cldr.Calendars.formatRelativeTimeField(-1, dow, { context });
    }
    // Continue adding conditions..

    // Finally, format the date if no conditons matched
    return cldr.Calendars.formatDate(s, { context });
};

export default (startDate: number, endDate: number, cldr: CLDR) => {
  // const relativeDate = cldr.Calendars.formatRelativeTime(cldr.Calendars.toGregorianDate({ date: startDate }), 
  // cldr.Calendars.toGregorianDate({ date: endDate }));
  let relativeDate = '';
  let formattedDate;
  let output = '';
  const calendarToken = getCalendarToken(moment(endDate), moment(startDate));
  const cldrStartDate = cldr.Calendars.toGregorianDate({ date: startDate, zoneId: 'America/New_York'});
  const cldrEndDate = cldr.Calendars.toGregorianDate({ date: endDate, zoneId: 'America/New_York'});

  switch(calendarToken){
    case 'lastYear':
      formattedDate = cldr.Calendars.formatDate({ 
        date: endDate,
        zoneId: "America/New_York"
      }, { 
        context: 'standalone', 
        skeleton: 'MMMdYYYY'
      });
      output = formattedDate;
      break;
    case 'sameWeek':
      formattedDate = cldr.Calendars.formatDate({ 
        date: endDate,
        zoneId: "America/New_York"
      }, { 
        context: 'standalone', 
        skeleton: 'EEEEhm'
      });
      output = formattedDate;
      break;
    case 'lastDay':
    case 'nextDay':
    case 'sameDay':
    case 'lastWeek':
      relativeDate = getRelativeTimeField(cldrEndDate, cldrStartDate, cldr);
      formattedDate = cldr.Calendars.formatDate({ 
        date: endDate,
        zoneId: "America/New_York"
      }, { 
        context: 'standalone', 
        time: 'short'
      });
      output = `${relativeDate} ${formattedDate}`;
      break;
    case 'sameElse':
    default:
      formattedDate = cldr.Calendars.formatDate({ 
        date: endDate,
        zoneId: "America/New_York"
      }, { 
        context: 'standalone', 
        skeleton: 'yMMMd'
      });
      output = formattedDate;
      break;
  }
  return output;
};